import { Component, OnInit } from '@angular/core';

import { Router } from "@angular/router";

import { DataService } from '../services';

import * as moment from 'moment';

@Component({
  moduleId: module.id,
  selector: 'kk-today-gank',
  templateUrl: 'today-gank.component.html',
  styleUrls: ['today-gank.component.css']
})
export class TodayGankComponent implements OnInit {

  dataReady: boolean = false;
  categories: any[] = ['福利'];

  constructor(private router: Router, private dataService: DataService) {}

  ngOnInit() {
    this.dataService.fetchCategoryOfToday().subscribe(
      category => {
        console.log(JSON.stringify(category));
        var result = category.slice();
        result.splice(result.indexOf('福利'), 1);
        Array.prototype.push.apply(this.categories, result);
        this.onDataReady();
      }
    );
  }

  onDataReady() {
    this.dataReady = true;
    this.router.navigate(['/today', this.categories[0]]);
  }

}
